<template>
	<div id="send-gift">
		<common-header :title="'送礼物'"></common-header>
 		<div style="border-bottom: 1px solid #eee;">
    <Cell :title="'选择礼物'" @click="selectGift" :value="tips"/>
		</div>
		
		<div class="list-wrap" v-if="choosed">
			<div class="list-item">
				<div class="good-img">
					<img class="goods-img" :src="giftObj.imagePath" alt=""/>
				</div>
				<!-- 商品信息 start -->
				<div class="content">
					<div class="goods-name">{{giftObj.name}}</div>
					<div class="goods-norms">{{giftObj.spec}} {{giftObj.size}}</div>
					<div class="goods-prices">
						<span class="price"><em class="price-icon">￥</em>{{giftObj.price}}</span>
						<span class="num fl-right"><em>x</em>{{giftObj.num}}</span>
					</div>
				</div>
			</div>
		</div>
		
    <!--<Cell :title="'赠送数量'" :type="'count'" :num="value" :max="giftObj.num" @change="valueChange"/>-->
		<!--<div class="reason" @click="showOT">
			<span>退货原因</span><span :class="{selected:isSelected}">{{returnReason}}</span>
			<img class="rt-arrow" src="../../assets/arrow_gray.png"/>
		</div>-->
		
		<div class="refund">
			<div class="title">
				赠礼祝福
			</div>
			<div class="refund-text">
				<textarea name="refund" placeholder="给朋友送个祝福吧~" maxlength="200" @input="getTextarea" ref="textArea" v-model="textContent"></textarea>
				<span>{{textNum}}/200</span>
			</div>
		</div>
		
		<div class="uploadImg">
    	<UploadImg @getIds="getIds" :maxImgNum='3'/>
		</div>
		
		<div class="submit-btn" :class="{active:choosed}" @click="submitApplication">送给好友</div>
    <!--<div class="shadow" v-if="showOrderType">
      <BottomPopup :itemList="itemList" @selItem="getBottomPopup"></BottomPopup>
    </div>-->
    
    
	</div>
</template>

<script>
import BottomPopup from '@/components/Popup/BottomPopup'
import CommonHeader from '@/components/Header/CommonHeader'
import Cell from '@/components/Cell/Cell'
import UploadImg from '@/components/UploadImg/UploadImg'


	export default {
		data() {
			return {
				textNum:0,
        showOrderType: false,
        returnReason:'请选择退货原因',
        isSelected:false,
        value: 1,
        choosed:false,
        tips:'未选择',
        giftObj:'',
        textContent:'',
        shareInfo:{},
        imgIdList:[],
			}
		},
		components: {
				BottomPopup,
				CommonHeader,
				Cell,
				UploadImg,
		},
		created(){
//			let giftObj = this.$route.params.giftObj
//			let giftObj = JSON.parse( localStorage.getItem('giftObj') )
			if(this.$route.query.giftObj){
				let giftObj = JSON.parse(this.$route.query.giftObj)
				console.log(giftObj)
				if(giftObj){
					this.giftObj = giftObj
					this.choosed = true
					this.tips = '已选择'
				}
			}
		},
		methods:{
			getTextarea(){
				let textNum = this.$refs.textArea.value.length
				this.textNum = textNum
			},
			showOT(){
        this.showOrderType = true
      },
			getBottomPopup(res){
				console.log(res);
				if(res != 'cancel'){
					this.returnReason = res.text
					this.isSelected = true
				}
				this.showOrderType = false
			},
			submitApplication(){
				if(this.choosed){
					this.getGiftSubmit()
					this.$popup({
		        content:'请点击右上角进行分享',
		        showCancel:false
		      }).then((res) => {
		        console.log(res)
		        if(res == 'sure'){
		        }
		      })
				}
			},
			selectGift(){
				this.$router.replace({name:'SendGiftList'})
			},
			valueChange(val) {
        this.value += val;
     	},
     	
     	getIds(ids){
    		console.log(ids)
    		this.imgIdList = ids
    	},
    	getGiftSubmit(){
    		let imageIds = ''
    		if(this.imgIdList && this.imgIdList.length > 0){
    			imageIds = this.imgIdList.join(',')
    		}
    		let params = {
    			a : 4,
    			targetId : 0,
    			content : this.textContent,
    			gifts:[{storeId:this.giftObj.storeId,num:this.giftObj.num}],
    			imageIds:imageIds
    		}
    		
    		this.$Api.getGiftSubmit(params).then((res)=>{
    			console.log(res)
    			if(res.q.s == 0){
    				this.shareInfo = res.q.shareInfo
    				this.initShare()
    			}
    		})
    	},
    	
    	initShare(){
    		console.log("https://"+location.hostname+this.shareInfo.shareUrl+"&type="+this.$route.query.type)
				let params = {
					title :this.shareInfo.shareTitle,
					desc  :this.shareInfo.shareText,
					link  :"https://"+location.hostname+this.shareInfo.shareUrl+"&type="+this.$route.query.type+"&fromShare=1",
					imgurl:this.shareInfo.iconPath
				}
	//				console.log(this.$wechat.iniShare)
				this.$wechat.iniShare(params,()=>{
					setTimeout(()=>{
						this.$router.replace({name:'SendGiftList'})
					},1000)
				})
			},
		}

	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#send-gift {
		height: 100%;
		/*background: #f0f0f0;*/
		.list-wrap {
			background: #fff;
			border-top:1px solid $border-color;
		}
		.shop-info {
			position: relative;
			width: 100%;
			height: 1.17rem;
			line-height: 1.17rem;
			padding: 0 0.4rem;
			font-size: 0.37rem;
			color: #333;
			&:after {
				@include bottom;
			};
			.shop-label {
				color: $label-color;
			}
		}
		.active {
			color: #fe4436;
		}
		.negative {
			color: #999;
		}
		.normal {}
		.list-item {
			position: relative;
			display: flex;
			padding: 0.21rem 0.32rem;
			/*border-bottom: 1px solid #cccccc;*/
			&:after {
				@include bottom;
			};
			.good-img {
				width: 2.13rem;
				height: 2.13rem;
				.goods-img {
					width: 2.13rem;
					height: 2.13rem;
					border: 1px solid #dedede;
					border-radius: 0.11rem;
				}
			}
			.content {
				flex: 1;
				margin-left: 0.35rem;
				display: flex;
				justify-content: space-around;
				flex-direction: column;
				.goods-name {
					font-size: 0.37rem;
					color: $text-color;
					@include textflow-two;
				}
				.goods-norms {
					color: $text-color2;
					font-size: 0.32rem;
					@include textflow-one;
				}
				.goods-prices {
					.price {
						color: $price-color;
						font-size: 0.43rem;
						.price-icon {
							font-size: 0.32rem;
						}
					}
					.num {
						color: $text-color2;
						font-size: 0.32rem;
						>em {
							font-size: 0.24rem;
						}
					}
				}
			}
		}
		.reason{
			position: relative;
			height: 1.17rem;
			line-height: 1.17rem;
			width: 100%;
			padding: 0 0.4rem;
			border-bottom:1px solid $border-color;
			background: #fff;
			>span{
				&:nth-of-type(1){
					font-size: 0.4rem;
					color: #333;
				};
				&:nth-of-type(2){
					float: right;
					font-size: 0.37rem;
					padding-right: 0.45rem;
					color: #999;
				};
			}
			.selected{
				color: #666 !important;
			}
			.rt-arrow{
				@include rt-arrow-sm;
			}
		}
		.refund{
			position: relative;
			min-height: 3.33rem;
			background: #fff;
			padding: 0.4rem 0.4rem 0.24rem;
			/*border-top:1px solid $border-color;*/
			.title{
				font-size: 0.4rem;
				color: #333;
				>span{
					padding-left: 0.13rem;
					font-size: 0.35rem;
					color: #666;
				}
			}
			textarea{
				@include textarea;
				font-size: 0.37rem;
				padding:0.27rem 0;
				height: 2.08rem;
			}
			.refund-text{
				>span{
					position: absolute;
					right: 0.4rem;
					bottom: 0.24rem;
					font-size: 0.35rem;
					color: #999;
				}
			}
		}
		.uploadImg{
			/*margin-top: 0.27rem;*/
			border-top: 1px solid $border-color;
			width: 100%;
			background: #fff;
			.title{
				font-size: 0.4rem;
				color: #333;
				padding: 0.37rem 0.4rem 0;
			}
			.publish-imgs{
				padding: 0.51rem 0 0.55rem 0.4rem;
			}
			ul{
				>li{
					position: relative;
					float: left;	
					margin-right: 0.4rem;
				}
				.del-btn{
					position: absolute;
					top: 0;
					right: 0;
					width: 0.43rem;
					height: 0.43rem;
					background: rgba(0, 0, 0, 0.3);
				}
				input{
					position: absolute;
					top: 0;
					left: 0;
					opacity: 0;
					width: 2.8rem;
					height: 2.8rem;
				}
				.publish-icon{
					width: 2.8rem;
					height: 2.8rem;
				}
			}
		}
		.submit-btn{
			position: fixed;
			bottom: 0;
			width: 100%;
			font-size: 0.45rem;
			height: 1.31rem;
			line-height: 1.31rem;
			text-align: center;
			color: #fff;
			background: #ccc;

		}
		.submit-btn.active{
			@include bg-color;
		}
	}
</style>


